.BadgeNew__hero {
    width: 100%;
    padding: 2rem 0;
    background: url(/static/media/stars.1d17360a.svg), #1B1B25;
    background-repeat: repeat;
    margin-bottom: 1rem;
    color: #ffffff;

    display: flex;
    justify-content: center;
}
.Badge {
    background: #FFFFFF;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.10);
    border-radius: 8px 8px 8px 8px;
    overflow: hidden;
    height: auto;
}

.Badge__header {
    padding: 0.5rem 0;
    height: 80px;
    background: #1B1B25;
    display: flex;
    justify-content: center;
}

.Badge__section-name {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 0;
}

.Badge__section-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0.5rem 0;
    background: #F4F4F7;
}


.Badge__avatar {
    border-radius: 50%;
    margin-right: 1rem;
    width: 120px;
    height: 120px;
}

.Badge__footer {
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #98CA3F;
    font-weight: bold;
    font-style: italic;
}


.BadgesListItem {
    justify-content: space-between;
    box-shadow: 2px 1px 2px 0px #00000075;
    margin: 1rem;
    display: flex;
    padding: 1rem;
    width: 360px;
}

.BadgesListItem__avatar {
    border-radius: 50%;
}

.list-unstyled {
    display: flex;
    flex-wrap: wrap;
}
.Navbar {
    width: 100%;
    padding: 0.5rem;
    background-color: #1C3643;
}

.Navbar__brand {
    color: #ffffff;
    align-items: center;
    display: flex;
    align-items: center;
    text-decoration: none;

}

.Navbar__brand:hover {
    color: #ffffff;
    text-decoration: none;
}

.Navbar__brand-logo {
    margin-right: 0.5rem;
}
.Home__Container {
    background: url(/static/media/stars.1d17360a.svg), #1B1B25;
    width: 100%;
    height: calc(100vh - 60px);
    display: flex;
}

.intro,
.logo {
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    flex-direction: column;
}

.intro {
    align-items: flex-start;
}

.intro img {
    width: 90%;
    padding-bottom: 2rem;

}

.intro .btn-lg {
    padding: 0.5rem 4rem;
    margin-top: 1rem;
}
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7;
}

h1 {
  font-family: 'Anton', sans-serif;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}
